import React, {useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import {restoreState} from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

type intervalType=ReturnType<typeof setInterval>

function Clock() {
const[disable,setDisable]=useState(false)
    const [timerId, setTimerId] = useState<number | undefined | intervalType>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)

    const start = () => {
        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)
        let timer = setInterval(()=>{
            setDate(new Date(restoreState('hw9-date', Date.now())));
            }, 1000);
        setTimerId(timer)
        setDisable(true)
    }

    const stop = () => {
        //   //    ,    (timerId <- undefined)
        clearTimeout(timerId);
        setDisable(false)
    }

    const onMouseEnter = () => { //   //     
        setShow(true)
    }
    const onMouseLeave = () => { //   //      
        setShow(false)
    }

    const stringTime = new Intl.DateTimeFormat("ru",{hour: "numeric",
        minute: "numeric",
        second: "numeric"}).format(date) || <br/> // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
    const stringDate = new Intl.DateTimeFormat("ru").format(date) || <br/> // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const stringDay = new Intl.DateTimeFormat('en',{weekday:'long'}).format(date) || <br/> //  
    const stringMonth = new Intl.DateTimeFormat('en',{month:'long'}).format(date) || <br/> //  

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    disabled={disable} //   //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    disabled={!disable} //   //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
